import {useRoute, useNavigation} from '@react-navigation/native';
import {Icon, List, TopNavigationAction} from '@ui-kitten/components';
import {useEffect, useMemo, useRef, useState} from 'react';
import {useTranslation} from 'react-i18next';
import {IconRejectReview, IconWorkflow} from '../../../assets/images';
import {useAppDispatch, useAppSelector} from '../../../hooks/useRedux';
import HeaderCM from '../../components/Header/HeaderCM';
import ModalFullScreen from '../../components/ModalFullScreen';
import StepCM from '../review-eid/components/Step';
import {SafeAreaView} from 'react-native-safe-area-context';
import {KeyboardAwareScrollView} from 'react-native-keyboard-aware-scroll-view';
import {Platform, Pressable, TouchableOpacity, View} from 'react-native';
import TextCM from '../../components/Text';
import InfoBodyScreen from '../review-eid/components/Info';
import {IRecipientTicketImpl} from '../../../model/interface/service.interface';
import {DateUtils} from '../../../utils';
import NavigationName from '../../../constants/NavigationName';
import {Status} from '../../../model/enum/common.enum';
import ButtonCM from '../../components/Button';
import {isEmpty} from 'lodash';
import TextFieldCM from '../../components/TextField';
import {useReviewServices} from '../../../hooks/apiHooks/useReviewTicketServices';
import Toast from 'react-native-toast-message';
import {ticketActions} from '../../../redux/slices';
import {useReviewEcontractServices} from '../../../hooks/apiHooks/useReviewEcontractServices';
import {Font} from '../../../constants';
import styles from './styles';
import {IPostMessageCollabo} from '../review-flow/interface';
import {IDetailFile} from '../review-econtract-flow/review-econtract/interface';
import ListItemField from '../review-econtract-flow/review-econtract/components/ListItemDoc';
import DocumentPicker from 'react-native-document-picker';

const ReviewEcollaboFlow = () => {
  const {t} = useTranslation();
  const dispatch = useAppDispatch();
  const router = useRoute<any>();
  const inputRef = useRef<any>(null);
  const dataEcollaboFlow = router?.params?.dataUserTask as IPostMessageCollabo;
  const navigation = useNavigation<any>();
  const [isShow, setisShow] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [isShowInfo, setIsShowInfo] = useState<boolean>(false);
  const [isShowFile, setIsShowFile] = useState<boolean>(true);
  const [error, setError] = useState<{[key: string]: string}>({});
  const [note, setNote] = useState('');
  const [noteError, setNoteError] = useState('');
  const [isShowNote, setIsShowNote] = useState(true);
  const {
    dataTicketRedux,
    recipientDetaiImpl,
    formData,
    modeler,
    statusPartyRaw,
  } = useAppSelector(state => state.ticket);

  const [listFieldIdDocFromColla, setFieldIdDocFromColla] = useState<
    IDetailFile[]
  >([]);

  const [listDataFile, setListDataFile] = useState<any[]>([]);

  const [recipientDetaiImplLast] = useState<IRecipientTicketImpl>(
    recipientDetaiImpl?.find(
      data => data.userTaskId === dataEcollaboFlow?._userTaskId,
    ) as IRecipientTicketImpl,
  );

  const {dataAccount} = useAppSelector(state => state.account);
  const {accessToken} = useAppSelector(state => state.common);

  const ReviewTicketServices = useReviewServices();
  const ReviewEcontractServices = useReviewEcontractServices();

  useEffect(() => {
    getListFile();
  }, []);

  const dataInfo: any[] = useMemo(() => {
    return [
      {
        key: 'Người tạo',
        type: 'avatar',
        value: {
          name:
            '' + dataAccount.firstName + ' ' + dataAccount.lastName || '---',
          email: dataTicketRedux.createdBy || '---',
          avatarUrl: dataAccount?.imageUrl,
        },
      },
      {
        key: 'Tên giao dịch',
        type: 'normal',
        value: {
          name: dataTicketRedux?.title || '---',
        },
      },
      {
        key: 'Quy trình',
        type: 'normal',
        value: {
          name:
            (dataTicketRedux?.jsonXml &&
              JSON.parse(dataTicketRedux?.jsonXml).properties.name) ||
            '---',
        },
      },
      {
        key: 'Ngày tạo',
        type: 'normal',
        value: {
          name: DateUtils.convertDateToDDMMYYYY_H_M(
            dataTicketRedux?.createdDate,
          ),
        },
      },
      {
        key: 'Thông tin đầu vào',
        type: 'form',
        value: {
          name: formData?.formName,
          funcViewFrom: () => {
            navigation.navigate(NavigationName.ViewFormScreen, {
              isOnlyView: true,
              readOnly: true,
              versionId: modeler?.formVersion,
              viewFromDoc: true,
            });
          },
        },
      },
      {
        key: 'Trạng thái',
        type: 'status',
        value: {
          status: recipientDetaiImplLast?.recipientStatus || Status.processing,
        },
      },
    ];
  }, [dataTicketRedux, formData, modeler]);

  const renderRightAction = () => {
    return (
      <TopNavigationAction
        onPress={() => setisShow(true)}
        icon={<IconWorkflow width={20} height={20} />}
      />
    );
  };

  const getListFile = async () => {
    try {
      const dataFile = await ReviewTicketServices.getListFileReview(
        dataTicketRedux.id,
        dataEcollaboFlow._userTaskId,
        accessToken,
      );
      if (dataFile) {
        setListDataFile(dataFile);
        const valueListDetail = await Promise.all(
          dataFile.map((data: any) =>
            ReviewEcontractServices.getDetailCollabora(data.eCollaboDocumentId),
          ),
        );
        setFieldIdDocFromColla(
          valueListDetail.map(data => ({
            ...data,
            sourceSystem: dataFile.find(
              (file: any) => file.eCollaboDocumentId === data.fileId,
            )?.attrs
              ? 'Từ eRequest'
              : data.sourceSystem,
          })),
        );
      }
    } catch (error) {}
  };

  const handleReject = async () => {
    if (!note) {
      setIsShowNote(true);
      setNoteError(t('Không được để trống thông tin'));
      inputRef?.current?.focus();
      return;
    }
    setNoteError('');
    const body = {
      ...recipientDetaiImplLast?.recipient,
      reason: note,
    };
    const dataReject = await ReviewTicketServices.actionTicket(
      recipientDetaiImplLast?.id,
      recipientDetaiImplLast?.ticketId,
      recipientDetaiImplLast?.userTaskId,
      body,
      accessToken,
      'reject',
    );
    if (dataReject) {
      Toast.show({
        type: 'success',
        text1: t('CM.noti'),
        text2: 'Từ chối yêu cầu thành công',
      });
      dispatch(
        ticketActions.setDataTicket({
          ...dataTicketRedux,
          ticketStatus: Status.rejected,
        }),
      );
      navigation.goBack();
    }
  };

  const handleSubmit = async () => {};

  const handlePickerFile = async () => {
    try {
      const data = await DocumentPicker.pick({
        type: [
          DocumentPicker.types.doc,
          DocumentPicker.types.docx,
          DocumentPicker.types.xls,
          DocumentPicker.types.xlsx,
        ],
      });
      if (data) {
        const fileUri =
          Platform.OS === 'ios'
            ? data?.[0].uri.replace('file://', '')
            : data?.[0].uri;
        const file = {
          name: data[0].name,
          uri: fileUri,
          type: data[0].type,
        };
        const dataForm = new FormData();
        dataForm.append('file', file);
        const dataUpload = await ReviewEcontractServices.uploadFiles(
          dataTicketRedux.id,
          dataEcollaboFlow._userTaskId,
          dataForm,
        );
        if (dataUpload) {
          const dataGetInfoFile =
            await ReviewEcontractServices.getDetailCollabora(
              dataUpload.eCollaboDocumentId,
            );
          const convertData = {
            ...dataGetInfoFile,
            sourceSystem: dataUpload?.attrs
              ? 'Từ eRequest'
              : dataGetInfoFile.sourceSystem,
          };
          setFieldIdDocFromColla([convertData, ...listFieldIdDocFromColla]);
        }
      }
    } catch (error) {}
  };

  const deleteFile = async (fileId: string) => {
    const fileSelect = listDataFile.find(
      file => file.eCollaboDocumentId === fileId,
    );
    const dataResp = await ReviewEcontractServices.deleteFile(fileSelect.id);
    if (dataResp) {
      Toast.show({
        type: 'success',
        text1: t('CM.info'),
        text2: t('CM.fileDeletedSuccessfully'),
      });
      setFieldIdDocFromColla(
        listFieldIdDocFromColla.filter(data => data.fileId !== fileId),
      );
    } else {
      Toast.show({
        type: 'error',
        text1: t('CM.info'),
        text2: t('CM.fileDeleteFailed'),
      });
    }
  };

  return (
    <SafeAreaView edges={['bottom']} style={{flex: 1, backgroundColor: '#fff'}}>
      <HeaderCM
        style={{
          backgroundColor: '#fff',
        }}
        titleStyle={{
          color: '#00204D',
        }}
        fillIconBackLeft="#00204DB2"
        title="Thương thảo tài liệu"
        contentStyle={{
          backgroundColor: '#fff',
        }}
        renderContentRight={renderRightAction}
      />

      <KeyboardAwareScrollView style={{flex: 1}}>
        <View style={styles.ctnBody}>
          <View style={styles.ctnSubBody}>
            <Pressable
              style={styles.ctnHeader}
              onPress={() => setIsShowInfo(!isShowInfo)}>
              <Icon
                fill={'#6F2CED'}
                width={16}
                height={16}
                name={isShowInfo ? 'arrow-circle-down' : 'arrow-circle-right'}
              />
              <TextCM style={styles.textTitle}>Thông tin chung</TextCM>
            </Pressable>

            {isShowInfo && (
              <View style={{paddingHorizontal: 10, paddingBottom: 10}}>
                <InfoBodyScreen dataInfo={dataInfo} />
              </View>
            )}
          </View>
        </View>

        <View style={styles.ctnBody}>
          <View style={styles.ctnSubBody}>
            <Pressable
              style={styles.ctnHeader}
              onPress={() => setIsShowFile(!isShowFile)}>
              <Icon
                fill={'#6F2CED'}
                width={16}
                height={16}
                name={isShowFile ? 'arrow-circle-down' : 'arrow-circle-right'}
              />
              <TextCM style={styles.textTitle}>
                Danh sách tài liệu cần thương thảo
              </TextCM>
            </Pressable>

            {isShowFile && dataEcollaboFlow._status !== 'task-success' && (
              <Pressable onPress={handlePickerFile} style={styles.ctnUpload}>
                <Icon
                  fill={'#6F2CED'}
                  width={16}
                  height={16}
                  name={'cloud-upload'}
                />
                <TextCM style={styles.textUpload}>
                  Thêm tệp tài liệu thương thảo
                </TextCM>
              </Pressable>
            )}

            {isShowFile && (
              <View style={{paddingHorizontal: 10, paddingBottom: 10}}>
                {listFieldIdDocFromColla && (
                  <List
                    scrollEnabled={false}
                    data={listFieldIdDocFromColla}
                    renderItem={({item}) => (
                      <ListItemField
                        typeReview="eCollabo"
                        deleteFile={deleteFile}
                        dataFlow={dataEcollaboFlow}
                        data={item}
                      />
                    )}
                  />
                )}
              </View>
            )}
          </View>
        </View>

        {dataEcollaboFlow._status !== 'task-success' && (
          <View style={styles.ctnBody}>
            <View
              style={[
                styles.ctnSubBody,
                {
                  marginBottom: 16,
                },
              ]}>
              <Pressable
                style={styles.ctnHeader}
                onPress={() => setIsShowNote(!isShowNote)}>
                <Icon
                  fill={'#6F2CED'}
                  width={16}
                  height={16}
                  name={isShowNote ? 'arrow-circle-down' : 'arrow-circle-right'}
                />
                <TextCM style={styles.textTitle}>Ghi chú</TextCM>
              </Pressable>

              {isShowNote && (
                <View
                  style={{
                    paddingHorizontal: 10,
                    paddingBottom: 10,
                    rowGap: 4,
                  }}>
                  <TextFieldCM
                    ref={inputRef}
                    isRequired={true}
                    label={'Nhập ghi chú'}
                    value={note}
                    onChangeText={text => {
                      setNote(text);
                      setNoteError('');
                    }}
                    multiline
                    maxLength={400}
                    errorMessage={noteError}
                  />
                </View>
              )}
            </View>
          </View>
        )}
      </KeyboardAwareScrollView>
      <View
        style={{
          flexDirection: 'row',
          alignItems: 'center',
          justifyContent: 'flex-start',
          columnGap: 8,
          backgroundColor: '#F2ECFD',
          marginHorizontal: 16,
          padding: 4,
          borderRadius: 8,
        }}>
        <Icon fill={'#6F2CED'} name="alert-circle" width={16} height={16} />
        <TextCM
          style={{
            color: '#6F2CED',
            flex: 1,
          }}>
          Chỉ những người được phân quyền truy cập mới được phép truy cập vào
          tài liệu và thực hiện thương thảo. Vui lòng phân quyền truy cập khi
          muốn thực hiện thương thảo !
        </TextCM>
      </View>
      {(recipientDetaiImplLast?.recipientStatus === Status.processing ||
        recipientDetaiImplLast?.recipientStatus === null) && (
        <View style={styles.ctnFooter}>
          <>
            <TouchableOpacity onPress={handleReject}>
              <IconRejectReview width={36} height={36} />
            </TouchableOpacity>
            <ButtonCM
              disabled={isEmpty(error) ? false : true}
              style={{flex: 1}}
              label={t('CM.confirm')}
              onPress={handleSubmit}
              loading={isLoading}
            />
          </>
        </View>
      )}
      <ModalFullScreen
        isVisible={isShow}
        children={<StepCM onClose={() => setisShow(false)} />}
      />
    </SafeAreaView>
  );
};

export default ReviewEcollaboFlow;
