import {Alert, Image, Pressable, TouchableOpacity, View} from 'react-native';
import TextCM from '../../../../../components/Text';
import {IComments} from '../../interface';
import {IAccount} from '../../../../../../model/interface/account.interface';
import styles from './styles';
import {SizeDP} from '../../../../../../constants/Size';
import {DateUtils} from '../../../../../../utils';
import {NavigationName} from '../../../../../../constants';
import {IDataDropdown} from '../../../../../components/DropDown';
import {IconEdit, IconTrash} from '../../../../../../assets/images';
import React, {useEffect, useState} from 'react';
import {Icon, MenuItem, OverflowMenu, Spinner} from '@ui-kitten/components';
import {
  decodeUnicodeToEmoji,
  extractSpanList,
  splitStringWithSpans,
} from '../../helper';
import {useReviewServices} from '../../../../../../hooks/apiHooks/useReviewTicketServices';
import {useAppSelector} from '../../../../../../hooks/useRedux';
import Toast from 'react-native-toast-message';
import {useNavigation} from '@react-navigation/native';
import {useTranslation} from 'react-i18next';
import ModalFullScreen from '../../../../../components/ModalFullScreen';
import WebView from 'react-native-webview';
import {useTicketServices} from '../../../../../../hooks/apiHooks/useTicketServices';
type Props = {
  data: IComments;
  user: string;
  setComment: (text: string) => void;
  deleteComment: (commentId: string) => void;
  allComments: Map<any, IComments[]>;
  onReplyClick: (action: 'reply' | 'edit', commentEdit: IComments) => void;
  level: number;
};

const actionComment = [
  {
    id: 1,
    key: 'Sửa bình luận',
    value: 'edit',
    icon: <IconEdit width={SizeDP(20)} height={SizeDP(20)} />,
  },
  {
    id: 2,
    key: 'Xóa bình luận',
    value: 'trash',
    icon: <IconTrash width={SizeDP(20)} height={SizeDP(20)} />,
  },
];

const CommentItem = React.memo(
  ({
    data,
    user,
    setComment,
    deleteComment,
    allComments,
    onReplyClick,
    level,
  }: Props) => {
    const navigation = useNavigation<any>();
    const {t} = useTranslation();
    const [menuVisible, setMenuVisible] = useState(false);
    const [openModal, setOpenModal] = useState(false);
    const [showComment, setShowComment] = useState(false);
    const [isLoading, setIsLoading] = useState(false);
    const [urlWebview, setUrlWebview] = useState('');
    const [dataUser, setDataUser] = useState<IAccount>();
    const ReviewService = useReviewServices();
    const {accessToken} = useAppSelector(state => state.common);
    const childComments = allComments.get(data.id) || [];
    const ListServices = useTicketServices();
    const handleChangeAction = (item: IDataDropdown) => {
      switch (item.value) {
        case 'edit':
          toogleMenu();
          onReplyClick('edit', data);
          console.log('Edit');
          break;
        case 'trash':
          toogleMenu();
          deleteComment(data.id);
          console.log('Delete');
          break;
        default:
          break;
      }
    };

    useEffect(() => {
      getInfoUser();
    }, []);

    const getInfoUser = async () => {
      const data = await ListServices.getMultiLoginAccount([user]);
      if (data) {
        setDataUser(data[0]);
      }
    };

    const handleOnReply = (data: IComments) => {
      onReplyClick('reply', data);
      setShowComment(true);
    };

    const toogleShowComment = () => {
      setShowComment(!showComment);
    };

    const handleViewFile = async () => {
      try {
        setIsLoading(true);
        const formData = new FormData();
        formData.append('keyName', data.attachment);
        formData.append('bucketName', data.bucketName);
        const dataView = await ReviewService.viewFile(formData, accessToken);
        if (dataView?.code == 1) {
          // navigation.navigate(NavigationName.ViewFileScreen, {
          //   url: dataView.message,
          // });
          setUrlWebview(dataView.message);
          setOpenModal(true);
        } else {
          Alert.alert(t('CM.info'), t('CM.error'));
        }
      } catch (error) {
      } finally {
        setIsLoading(false);
      }
    };

    const handleGetContent = (content: string) => {
      const encodeContent = decodeUnicodeToEmoji(content);
      if (!encodeContent) {
        if (data.fileName) {
          return (
            <>
              <Pressable onPress={handleViewFile} style={styles.styleFile}>
                <TextCM numberOfLines={1} style={styles.content}>
                  {'' + data.fileName}
                </TextCM>
                {isLoading && <Spinner size="tiny" />}
              </Pressable>
            </>
          );
        } else {
          return (
            <>
              <TextCM style={styles.content}>'---'</TextCM>
            </>
          );
        }
      }
      if (!encodeContent.includes('class="user_tag"')) {
        if (!data.fileName) {
          return (
            <TextCM style={styles.content}>
              {decodeUnicodeToEmoji(encodeContent)}
            </TextCM>
          );
        } else {
          return (
            <>
              <TextCM style={styles.content}>
                {decodeUnicodeToEmoji(encodeContent)}
              </TextCM>
              <Pressable onPress={handleViewFile} style={styles.styleFile}>
                <TextCM numberOfLines={1} style={styles.content}>
                  {'' + data.fileName}
                </TextCM>
                {isLoading && <Spinner size="tiny" />}
              </Pressable>
            </>
          );
        }
      }
      if (encodeContent.includes('class="user_tag"')) {
        const listSplit = splitStringWithSpans(encodeContent);
        const filterData = listSplit.map(text =>
          !text.includes('class="user_tag"')
            ? text
            : `@${extractSpanList(text)?.[0]?.email}`,
        );
        return (
          <View style={styles.bodyView}>
            {filterData.map(text => {
              if (text.startsWith('@')) {
                return <TextCM style={styles.tag}>{text.slice(1)}</TextCM>;
              } else {
                return <TextCM style={styles.content}>{text}</TextCM>;
              }
            })}
          </View>
        );
      }
    };

    const toogleMenu = () => {
      setMenuVisible(!menuVisible);
    };

    return (
      <>
        <View style={styles.ctnMain}>
          <View style={styles.ctnImage}>
            <Image
              width={SizeDP(24)}
              height={SizeDP(24)}
              borderRadius={SizeDP(12)}
              source={{
                uri: dataUser?.imageUrl
                  ? dataUser?.imageUrl
                  : 'https://static.vecteezy.com/system/resources/previews/009/292/244/original/default-avatar-icon-of-social-media-user-vector.jpg',
              }}
            />
          </View>
          <View style={{flex: 1}}>
            <View style={styles.ctnSecond}>
              <TextCM style={styles.ctnName}>
                {'' + dataUser?.firstName + ' ' + dataUser?.lastName + `• `}
              </TextCM>
              <TextCM style={styles.ctnEmail}>{dataUser?.email}</TextCM>
            </View>
            <TextCM style={styles.ctnTime}>
              {DateUtils.convertDateToDDMMYYYY_H_M(data.lastModifiedDate)}
            </TextCM>
            {handleGetContent(data.content)}
            <View
              style={{
                flexDirection: 'row-reverse',
                alignItems: 'center',
                justifyContent: 'flex-end',
                columnGap: 10,
              }}>
              <Pressable
                onPress={() => handleOnReply(data)}
                hitSlop={SizeDP(15)}>
                <TextCM style={styles.ctnReply}>Trả lời</TextCM>
              </Pressable>
              {childComments.length > 0 && (
                <>
                  {!showComment ? (
                    <Pressable
                      onPress={toogleShowComment}
                      style={{
                        flexDirection: 'row',
                        alignItems: 'center',
                        justifyContent: 'flex-start',
                        columnGap: 6,
                        paddingVertical: 3,
                      }}>
                      <Icon
                        fill={'#00204DE5'}
                        name="message-circle-outline"
                        width={16}
                        height={16}
                      />
                      <TextCM
                        style={{
                          fontSize: 12,
                        }}>
                        Xem {childComments?.length} bình luận
                      </TextCM>
                    </Pressable>
                  ) : (
                    <Pressable
                      onPress={toogleShowComment}
                      style={{
                        flexDirection: 'row',
                        alignItems: 'center',
                        justifyContent: 'flex-start',
                        columnGap: 6,
                        paddingVertical: 3,
                      }}>
                      <TextCM
                        style={{
                          fontSize: 12,
                        }}>
                        Thu gọn
                      </TextCM>
                    </Pressable>
                  )}
                </>
              )}
            </View>
          </View>

          <View style={{}}>
            <OverflowMenu
              anchor={() => (
                <TouchableOpacity onPress={toogleMenu}>
                  <Icon width={20} height={20} name="more-vertical" />
                </TouchableOpacity>
              )}
              visible={menuVisible}
              onBackdropPress={toogleMenu}>
              {actionComment.map(action => (
                <MenuItem
                  key={action.value}
                  accessoryLeft={<Icon name={action.value} />}
                  title={action.key}
                  onPress={() => handleChangeAction(action)}
                />
              ))}
            </OverflowMenu>
          </View>
        </View>
        {showComment && (
          <View style={{marginLeft: 30, marginTop: 10}}>
            {childComments?.map(item => (
              <CommentItem
                key={item.id}
                data={item}
                user={item.createdBy}
                setComment={setComment}
                deleteComment={deleteComment}
                allComments={allComments}
                onReplyClick={onReplyClick}
                level={level + 1}
              />
            ))}
          </View>
        )}

        <ModalFullScreen
          isVisible={openModal}
          children={
            <View
              style={{
                backgroundColor: '#fff',
                height: '100%',
              }}>
              <View
                style={{
                  justifyContent: 'flex-end',
                  alignItems: 'flex-end',
                  marginRight: 16,
                  marginBottom: 10,
                }}>
                <Pressable onPress={() => setOpenModal(false)}>
                  <Icon name="close" width={24} height={24} />
                </Pressable>
              </View>
              <View style={{flex: 1}}>
                {urlWebview && (
                  <WebView startInLoadingState source={{uri: urlWebview}} />
                )}
              </View>
            </View>
          }
        />
      </>
    );
  },
);

export default CommentItem;
