export {
  BASE_URL,
  BASE_URL_FORM,
  BASE_URL_TICKET,
  BASE_URL_EKYC,
  BASE_URL_ECOLLABO,
};

interface IRequestFetch {
  url: string;
  method: 'GET' | 'POST' | 'PUT' | 'PATCH' | 'DELETE';
  body?: any;
  headers?: any;
  params?: any;
  typeOfResponse?: 'text' | 'json' | '';
}

const fetchRequest = async ({
  url = '',
  method,
  body,
  headers = {
    'Content-Type': 'application/json',
  },
  params,
  typeOfResponse = 'json',
}: IRequestFetch) => {
  try {
    let init: any = {
      method: method,
      headers: headers,
    };
    if (params) {
      url += `?${new URLSearchParams(params).toString()}`;
    }
    if (body) {
      init = {
        method: method,
        headers: headers,
        body: body,
      };
    }
    const dataResponse = await fetch(url, init);
    // error token
    if (dataResponse.status === 401) {
      // dispatch(commonActions.setIsAuthorized(false));
      // dispatch(commonActions.setAccessToken(''));
      // dispatch(accountActions.clearAccount());
      Toast.show({
        text1: 'Thông báo',
        text2: 'Phiên đăng nhập đã hết hạn. Vui lòng đăng nhập lại.',
        type: 'error',
      });
      return;
    }

    // error system
    if (
      dataResponse.status !== 200 &&
      dataResponse.status !== 202 &&
      dataResponse.status !== 201 &&
      dataResponse.status !== 204
    ) {
      return Promise.resolve({
        data: null,
        status: dataResponse.status,
        headers: dataResponse.headers,
      });
    }

    let data;
    switch (typeOfResponse) {
      case 'json':
        data = await dataResponse.json();
        break;
      case 'text':
        data = await dataResponse.text();
        break;
      default:
        data = dataResponse.status;
        break;
    }

    return Promise.resolve({
      data,
      status: dataResponse.status,
      headers: dataResponse.headers,
    });
  } catch (error) {
    console.log('err ' + url, error);
    return Promise.reject(error);
  }
};

export default fetchRequest;
