import {View, Image} from 'react-native';
import {Font} from '../../../../../constants';
import DropdownListCM from '../../../../components/DropDownList';
import TextCM from '../../../../components/Text';
import {optionPermisson} from '../share/helper';
import {IDataUpdate, IResourceShare} from '../share/interface';
import {OptionPermisson, PermType} from '../share/enum';
import {useState} from 'react';
import {Divider, Icon, IndexPath} from '@ui-kitten/components';
import {useTicketServices} from '../../../../../hooks/apiHooks/useTicketServices';
import Toast from 'react-native-toast-message';
import {Snackbar} from 'react-native-paper';
import ModalFullScreen from '../../../../components/ModalFullScreen';
import HeaderCM from '../../../../components/Header/HeaderCM';
import {KeyboardAwareScrollView} from 'react-native-keyboard-aware-scroll-view';
import TextFieldCM from '../../../../components/TextField';
import ButtonCM from '../../../../components/Button';

type Props = {
  dataResource: IResourceShare;
  dataUser: any;
  setListDataUpdate: any;
  toogleReload: () => void;
};

const ListResource = ({
  dataResource,
  dataUser,
  setListDataUpdate,
  toogleReload,
}: Props) => {
  const [listPermissionState, setListPermissionState] = useState<IndexPath>(
    new IndexPath(
      optionPermisson.findIndex(
        (data: any) => data.value === dataResource.perm,
      ),
    ),
  );

  const [visible, setVisible] = useState(false);
  const [description, setDescription] = useState('');

  const onToggleSnackBar = () => setVisible(!visible);

  const TicketServices = useTicketServices();

  const handleChangeLimitPer = async (data: IndexPath) => {
    const valueSelect = optionPermisson[data.row];
    setListPermissionState(data);
    if (valueSelect.value === OptionPermisson.delete) {
      await TicketServices.deleteAclsById(dataResource.id);
      toogleReload();
      return;
    }

    if (valueSelect.value === OptionPermisson.permission) {
      setDescription('');
      onToggleSnackBar();
      return;
    }
    setListPermissionState(data);
    const dataUpdate: IDataUpdate = {
      assignee: dataResource.assignee,
      assigneeId: dataResource.assigneeId,
      perm: valueSelect.value,
      permType: dataResource.permType,
      name: dataResource.name,
      avatar: dataResource.avatar,
    };
    setListDataUpdate((prev: IDataUpdate[]) => {
      const dataFind = prev.find(
        data => data.assigneeId === dataResource.assigneeId,
      );
      if (dataFind) {
        return prev.map(data =>
          data.assigneeId === dataResource.assigneeId ? dataUpdate : data,
        );
      } else {
        return [...prev, dataUpdate];
      }
    });
  };

  const getName = () => {
    if (dataResource?.name?.trim()) {
      return dataResource?.name?.trim();
    }
    if (dataUser) {
      return '' + dataUser?.firstName + ' ' + dataUser?.lastName;
    }
  };

  const handleSend = async () => {
    const valueSelect = optionPermisson[listPermissionState.row];
    const params = {
      resourceId: dataResource.resourceId,
    };
    const body = {
      aclForm: {
        assignee: dataResource.assignee,
        assigneeId: dataResource.assigneeId,
        perm: valueSelect.value,
        permType: dataResource.permType,
        name: dataResource.name,
      },
      description: description,
    };

    const dataResp = await TicketServices.pushlishFile(params, body);
    if (dataResp) {
      onToggleSnackBar();
      setListPermissionState(
        new IndexPath(
          optionPermisson.findIndex(
            (data: any) => data.value === dataResource.perm,
          ),
        ),
      );
    }
  };

  const handleBack = () => {
    setListPermissionState(
      new IndexPath(
        optionPermisson.findIndex(
          (data: any) => data.value === dataResource.perm,
        ),
      ),
    );
    onToggleSnackBar();
  };

  return (
    <View
      style={{
        flex: 1,
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'space-between',
        marginVertical: 10,
        backgroundColor: '#fff',
        columnGap: 10,
      }}>
      <View
        style={{
          flexDirection: 'row',
          alignItems: 'center',
          columnGap: 8,
          flex: 1,
        }}>
        {dataResource.permType !== PermType.orgin ? (
          <Image
            width={32}
            height={32}
            borderRadius={16}
            source={{
              uri:
                dataResource.avatar ||
                'https://static.vecteezy.com/system/resources/previews/009/292/244/original/default-avatar-icon-of-social-media-user-vector.jpg',
            }}
          />
        ) : (
          <View
            style={{
              width: 32,
              height: 32,
              borderRadius: 16,
              backgroundColor: '#08c',
              alignItems: 'center',
              justifyContent: 'center',
            }}>
            <Icon fill={'#fff'} width={26} height={26} name="people" />
          </View>
        )}
        <View style={{flex: 1}}>
          {dataResource.permType !== PermType.orgin && (
            <View>
              <TextCM
                style={{
                  flex: 1,
                  fontSize: 14,
                  color: '#00204DF2',
                  fontFamily: Font.InterRegular400,
                  justifyContent: 'center',
                  alignItems: 'center',
                }}>
                {getName()}
              </TextCM>
            </View>
          )}
          <View>
            <TextCM
              style={{
                flex: 1,
                fontSize: 12,
                justifyContent: 'center',
                alignItems: 'center',
                color: '#00204DB2',
                fontFamily: Font.InterRegular400,
              }}>
              {dataResource.assignee}
            </TextCM>
          </View>
        </View>
      </View>
      <View style={{flex: 0.7}}>
        {dataResource.perm === OptionPermisson.owner ? (
          <View
            style={{
              backgroundColor: '#EDF2FD',
              padding: 8,
              borderRadius: 4,
            }}>
            <TextCM
              style={{
                color: '#366AE2',
                fontFamily: Font.InterRegular400,
                fontSize: 12,
                textAlign: 'center',
              }}>
              Chủ sở hữu
            </TextCM>
          </View>
        ) : (
          <DropdownListCM
            isi18n={true}
            size="medium"
            onSelect={handleChangeLimitPer}
            valueSelect={listPermissionState}
            listData={
              dataResource.permType !== PermType.orgin
                ? optionPermisson
                : optionPermisson.filter(
                    per => per.value !== OptionPermisson.permission,
                  )
            }
          />
        )}
      </View>

      <ModalFullScreen
        isVisible={visible}
        children={
          <>
            <HeaderCM
              style={{
                backgroundColor: '#fff',
              }}
              titleStyle={{
                color: '#00204D',
              }}
              fillIconBackLeft="#00204DB2"
              title="Chia sẻ"
              contentStyle={{
                backgroundColor: '#fff',
              }}
              onPressIconLeft={handleBack}
            />
            <Divider />
            <KeyboardAwareScrollView style={{padding: 16, rowGap: 10}}>
              <View
                style={{
                  flexDirection: 'row',
                  alignItems: 'center',
                  columnGap: 8,
                  flex: 1,
                  marginBottom: 16,
                }}>
                {dataResource.permType !== PermType.orgin ? (
                  <Image
                    width={32}
                    height={32}
                    borderRadius={16}
                    source={{
                      uri:
                        dataResource.avatar ||
                        'https://static.vecteezy.com/system/resources/previews/009/292/244/original/default-avatar-icon-of-social-media-user-vector.jpg',
                    }}
                  />
                ) : (
                  <View
                    style={{
                      width: 32,
                      height: 32,
                      borderRadius: 16,
                      backgroundColor: '#08c',
                      alignItems: 'center',
                      justifyContent: 'center',
                    }}>
                    <Icon fill={'#fff'} width={26} height={26} name="people" />
                  </View>
                )}
                <View style={{flex: 1}}>
                  {dataResource.permType !== PermType.orgin && (
                    <View>
                      <TextCM
                        style={{
                          flex: 1,
                          fontSize: 14,
                          color: '#00204DF2',
                          fontFamily: Font.InterRegular400,
                          justifyContent: 'center',
                          alignItems: 'center',
                        }}>
                        {getName()}
                      </TextCM>
                    </View>
                  )}
                  <View>
                    <TextCM
                      style={{
                        flex: 1,
                        fontSize: 12,
                        justifyContent: 'center',
                        alignItems: 'center',
                        color: '#00204DB2',
                        fontFamily: Font.InterRegular400,
                      }}>
                      {dataResource.assignee}
                    </TextCM>
                  </View>
                </View>
              </View>

              <TextFieldCM
                value={description}
                onChangeText={setDescription}
                label="Nhập lời nhắn"
                maxLength={100}
                multiline
              />
            </KeyboardAwareScrollView>
            <View
              style={{
                flexDirection: 'row',
                alignItems: 'center',
                justifyContent: 'flex-start',
                columnGap: 8,
                backgroundColor: '#F2ECFD',
                padding: 4,
                borderRadius: 8,
                marginHorizontal: 16,
                marginBottom: 16,
              }}>
              <Icon
                fill={'#6F2CED'}
                name="alert-circle"
                width={16}
                height={16}
              />
              <TextCM
                style={{
                  color: '#6F2CED',
                  flex: 1,
                }}>
                Người được chỉ định sẽ có quyền chuyển trạng thái tài liệu và
                thực hiện thương thảo tài liệu đó
              </TextCM>
            </View>
            <View style={{paddingHorizontal: 16}}>
              <ButtonCM label="Gửi lời mời" onPress={handleSend} />
            </View>
          </>
        }
      />
    </View>
  );
};

export default ListResource;
